* {
  box-sizing: border-box;
}
  
  body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.game-title{
    width: 400px;
    margin: 50px auto -20px auto;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    /* align-items: center; */
    -webkit-flex-wrap: row-wrap;
            flex-wrap: row-wrap;
}

.game-title h1 {
    font-weight: 800;
}

.game-body{
    width: 400px;
    margin: 20px auto;
    /* display: flex; */
    -webkit-justify-content: center;
            justify-content: center;
    /* align-items: center; */
    -webkit-flex-wrap: row-wrap;
            flex-wrap: row-wrap;
}


.bd {
    border: 1px solid #3c3c3c;
}

.status-box {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    padding-top: 20px;
    padding-bottom: 20px;
    font-size: 1.8rem;
    font-weight: 800;
    background-color: red;
    color: white;
}


.form-box {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    margin-top: 30px;
    margin-bottom: 10px;
    font-size: 1.5rem;
    font-weight: 800;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    /* display: flex; */
}

.form-box input, .form-box button {
    width: 80%;
    line-height: 50px;
    margin-bottom: 20px;
    font-size: 1.4rem;
}

.form-box button {
    color: #DEDEDE;
    background-color: #3C3C3C;
    font-weight: 800;
    font-size: 1.7rem;
}



.count-box {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    padding-top: 0px;
    padding-bottom: 20px;
    font-size: 1.5rem;
    font-weight: 700;
}

.count-box span {
    font-size: 2.7rem;
    color: orange;
}
.hist-box {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    padding-top: 20px;
    padding-bottom: 20px;
    background-color: #11a8ab;
    -webkit-flex-flow: wrap;
            flex-flow: wrap;
}

.num-box{
    background-color: #1a4e95;
    color: white;
    padding: 5px;
    margin: 3px;
    font-size: 1.2rem;

    font-weight: 800;
}

.game-top {
    /* display: flex;
    justify-content: right; */
    /* width: 100%; */
}
.game-top a:link, 
.game-top a:visited {
    display: inline-block;
    width: 100%;
    font-size: 1.5rem;
    font-weight: 800;
    text-align: right;
} 

