.form-box {
    width: 100%;
    display: flex;
    justify-content: center;
    margin-top: 30px;
    margin-bottom: 10px;
    font-size: 1.5rem;
    font-weight: 800;
    display: flex;
    flex-wrap: wrap;
    /* display: flex; */
}

.form-box input, .form-box button {
    width: 80%;
    line-height: 50px;
    margin-bottom: 20px;
    font-size: 1.4rem;
}

.form-box button {
    color: #DEDEDE;
    background-color: #3C3C3C;
    font-weight: 800;
    font-size: 1.7rem;
}


